<template>
  <div class="container1">
    <el-card shadow="never">
      <div class="header">
        <div class="left">
          <el-form ref="form" :model="listObj" label-width="80px">
            <el-form-item label="关键字">
              <el-input
                placeholder="根据文章标题搜索"
                v-model="listObj.keyword"
              ></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="listObj.state" placeholder="请选择">
                <el-option label="启用" value="1"></el-option>
                <el-option label="禁用" value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <el-button class="clear" @click="clear">清除</el-button>
          <el-button type="primary" @click="search">搜索</el-button>
        </div>
        <div class="right">
          <el-button type="success" icon="el-icon-edit" @click="addSkills"
            >新增技巧</el-button
          >
        </div>
      </div>
      <el-alert type="info" show-icon :closable="false">
        <span slot="title">数据一共{{ total }}条</span>
      </el-alert>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="date" type="index" label="序号" width="90">
        </el-table-column>
        <el-table-column label="文章标题">
          <template slot-scope="scope">
            {{ scope.row.title }}
            <i
              class="el-icon-film"
              @click="video(scope.row.videoURL)"
              style="color:blue;  font-size: 18px;"
              v-if="scope.row.videoURL"
            ></i>
          </template>
        </el-table-column>
        <el-table-column prop="visits" label="阅读数"> </el-table-column>
        <el-table-column prop="username" label="录入人"> </el-table-column>
        <el-table-column label="录入时间">
          <template slot-scope="scope">
            {{
              scope.row.createTime
                | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}')
            }}
          </template>
        </el-table-column>
        <el-table-column prop="state" label="状态">
          <template slot-scope="scope">
            <span>{{ scope.row.state == 0 ? '已禁用' : '已启用' }}</span>
          </template>
        </el-table-column>
        <!-- 操作 -->
        <el-table-column prop="address" label="操作" width="240">
          <template slot-scope="scope">
            <el-button @click="view(scope.row)" type="text" size="mini"
              >预览</el-button
            >
            <el-button
              type="text"
              size="mini"
              @click="handlestate(scope.row.id, scope.row.state)"
              >{{ scope.row.state == 0 ? '启用' : '禁用' }}</el-button
            >
            <el-button
              v-if="scope.row.status != 'deleted'"
              size="mini"
              type="text"
              @click="edit(scope.row)"
              :disabled="scope.row.state == 0 ? false : true"
              >修改</el-button
            >
            <el-button
              v-if="scope.row.status != 'deleted'"
              size="mini"
              type="text"
              @click="removeArtiles(scope.row.id)"
              :disabled="scope.row.state == 0 ? false : true"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page="currentPage4"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="100"
        layout="  prev, pager, next, sizes, jumper"
        :total="total"
      >
      </el-pagination>
      <!-- 添加技巧对话框 -->
      <el-dialog
        title="新增文章"
        :visible.sync="dialogVisible"
        width="50%"
        :before-close="handleClose"
      >
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item
            label="文章标题"
            placeholder="请输入文章标签"
            prop="name"
          >
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="文章内容">
            <quillEditor
              :options="editorOption"
              v-model="content"
            ></quillEditor>
          </el-form-item>
          <el-form-item label="视频地址" placeholder="请输入视频地址">
            <el-input v-model="ruleForm.url"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sureAdd">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 视频弹框 -->
      <el-dialog
        :visible.sync="centerDialogVisible"
        width="50%"
        center
        class="videobox"
        :show-close="flag2"
      >
        <!-- style="padding:0" -->
        <span slot="title"
          ><i
            @click="closevideo"
            style="font-size:60px"
            class="el-icon-error"
          ></i
        ></span>
        <!-- <video-player
          class="video-player vjs-custom-skin"
          ref="videoPlayer"
          :playsinline="true"
          :options="playerOptions"
        >
        </video-player> -->
        <div class="v_body">
          <video :src="videoURL" class="video" autoplay="autoplay"></video>
        </div>
      </el-dialog>
      <!-- 预览弹框 -->
      <el-dialog
        title="预览文章"
        :visible.sync="dialogVisible1"
        width="50%"
        :before-close="handleClose"
      >
        <div class="body">
          <div
            class="title"
            style="border-bottom: 1px dashed #ccc; padding-bottom: 10px;"
          >
            <h2>{{ viewobj.title }}</h2>
            <span>{{
              viewobj.createTime | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}')
            }}</span>
            <span style="margin:0 8px">{{ viewobj.username }}</span>
            <span><i class="el-icon-view"></i></span>
            <span style="margin-left:8px">{{ viewobj.visits }}</span>
          </div>
          <div
            class="content"
            style="background: #f5f5f5; padding: 10px;"
            v-html="viewobj.articleBody"
          ></div>
        </div>
      </el-dialog>
      <!-- 编辑弹框 -->
      <el-dialog
        title="修改文章"
        class="editbox"
        :visible.sync="dialogVisible2"
        width="50%"
        :before-close="handleClose"
      >
        <el-form
          :model="ruleForm1"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item
            label="文章标题"
            placeholder="请输入文章标签"
            prop="name"
          >
            <el-input v-model="ruleForm1.name"></el-input>
          </el-form-item>
          <el-form-item label="文章内容">
            <quillEditor
              :options="editorOption"
              v-model="content1"
            ></quillEditor>
          </el-form-item>
          <el-form-item label="视频地址" placeholder="请输入视频地址">
            <el-input v-model="ruleForm1.url"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible2 = false">取 消</el-button>
          <el-button type="primary" @click="sureEdite">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { list, changeState, remove, add, update } from '@/api/hmmm/articles.js'
// 视频插件
// import 'video.js/dist/video-js.css'
// import { videoPlayer } from 'vue-video-player'

// 富文本
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'

export default {
  components: {
    // videoPlayer,
    quillEditor
  },
  data () {
    return {
      flag2: false,
      listObj: {
        page: 1,
        pagesize: 10,
        keyword: '',
        state: null
      },
      //   表格
      tableData: [],
      // 分页
      currentPage4: 1,
      total: 0,
      // 添加技巧对话框
      dialogVisible: false,
      ruleForm: {
        name: '',
        url: ''
      },
      content: '',
      rules: {
        name: [{ required: true, message: '请输入文章标题', trigger: 'blur' }]
      },
      // 视频弹框
      videoURL: '',
      centerDialogVisible: false,
      //   playerOptions: {
      //     playbackRates: [0.5, 1.0, 1.5, 2.0], // 可选的播放速度
      //     autoplay: true, // 如果为true,浏览器准备好时开始回放。
      //     muted: false, // 默认情况下将会消除任何音频。
      //     loop: false, // 是否视频一结束就重新开始。
      //     preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
      //     language: 'zh-CN',
      //     aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
      //     fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
      //     sources: [
      //       {
      //         type: 'video/mp4', // 类型
      //         src: '' // url地址
      //       }
      //     ],
      //     notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
      //     controlBar: {
      //       timeDivider: false, // 当前时间和持续时间的分隔符
      //       durationDisplay: false, // 显示持续时间
      //       remainingTimeDisplay: false, // 是否显示剩余时间功能
      //       fullscreenToggle: false // 是否显示全屏按钮
      //     }
      //   },
      // 富文本编辑框配置项
      editorOption: {
        modules: {
          toolbar: [
            [
              'bold',
              'italic',
              'underline',
              'strike',
              { list: 'ordered' },
              { list: 'bullet' },
              'blockquote',
              'code-block',
              'image',
              'link'
            ]
          ]
        }
      },
      // 预览弹框
      dialogVisible1: false,
      viewobj: {},
      // 编辑弹框
      dialogVisible2: false,
      ruleForm1: {
        name: '',
        url: '',
        id: ''
      },
      content1: ''
    }
  },
  created () {
    this.getList()
  },
  methods: {
    // 获取列表
    async getList () {
      const res = await list(this.listObj)
      this.tableData = res.data.items
      this.total = res.data.counts
    },
    // 分页
    handleSizeChange (val) {
      this.listObj.pagesize = val
    },
    handleCurrentChange (val) {
      this.currentPage4 = val
      this.listObj.page = val
      this.getList()
    },
    // 启用禁用
    async handlestate (id, state) {
      await changeState({ id, state: state == 0 ? 1 : 0 })
      this.getList()
      this.$message({
        message: '操作成功',
        type: 'success'
      })
    },
    // 添加
    addSkills () {
      this.dialogVisible = true
    },
    // 点击确认添加
    async sureAdd () {
      var obj = {
        articleBody: this.content,
        title: this.ruleForm.name,
        videoURL: this.ruleForm.url
      }
      await add(obj)
      this.listObj.page = 1
      this.currentPage4 = 1
      this.getList()
      this.dialogVisible = false
      // 清空表单
      this.content = ''
      this.ruleForm.name = ''
      this.ruleForm.url = ''
    },

    handleClose () {
      this.dialogVisible = false
      this.dialogVisible1 = false
      this.dialogVisible2 = false
    },
    // 搜索
    async search () {
      this.getList()
    },
    // 清除
    clear () {
      ;(this.listObj.keyword = ''), (this.listObj.state = null)
    },
    // 文章删除
    removeArtiles (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          remove({ id })
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getList()
        })
        .catch(() => {})
    },
    // 视频
    video (videoURL) {
      this.centerDialogVisible = true
      //   this.playerOptions.sources[0].src = videoURL
      this.videoURL = videoURL
    },
    closevideo () {
      this.centerDialogVisible = false
    },
    your_method () {},
    // 图片预览
    view (row) {
      this.dialogVisible1 = true
      this.viewobj = row
    },
    // 编辑
    edit (row) {
      this.dialogVisible2 = true
      this.ruleForm1.name = row.title
      this.content1 = row.articleBody
      this.ruleForm1.url = row.videoURL
      this.ruleForm1.id = row.id
    },
    async sureEdite () {
      var obj = {
        id: this.ruleForm1.id,
        articleBody: this.content1,
        title: this.ruleForm1.name,
        videoURL: this.ruleForm1.url
      }
      await update(obj)
      this.getList()
      this.dialogVisible2 = false
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.container1 {
  padding: 10px;
  //   width: 0;
  .el-card {
    .header {
      display: flex;
      justify-content: space-between;

      .left {
        display: flex;

        .el-form {
          display: flex;
        }

        .clear {
          margin-left: 10px;
        }

        .el-button {
          height: 36px;
        }
      }
    }

    .el-alert {
      margin-bottom: 20px;
    }

    .el-pagination {
      float: right;
      margin: 25px;
    }
    .el-button--mini,
    .el-button--small {
      font-size: 14px;
      margin: 10px;
      border-radius: 3px;
    }

    ::v-deep .ql-editor {
      height: 300px;
    }

    .content[data-v-f6f15582] {
      background: #f5f5f5;
      padding: 10px;
    }

    .videobox {
      //   background-color: transparent;
      //   background-color: #808080;
      background: rgba(0, 0, 0, 0.3);
      padding: 0;
      ::v-deep .el-dialog__header {
        background-color: transparent;
        position: absolute;
        width: 100%;
        top: -78px;
        z-index: 100;
        padding: 0;
      }
    }

    ::v-deep .el-dialog__body {
      padding: 0 20px;
      margin-top: 20px;
    }

    ::v-deep .video {
      position: relative;
      width: 800px;
      left: -45px;
    }

    ::v-deep img {
      width: 100%;
    }

    ::v-deep .el-dialog__footer {
      text-align: right;
    }
  }
}
</style>
